// @flow

import blogService from '../services/blogs'

const reducer = (blogs : Array<Object> = [], action : Object) : Array<Object> => {
  let old : Array<Object>

  switch (action.type) {
  case 'LIKE_BLOG':
    old = blogs.filter(b => b._id !== action.blog._id)
    const liked : Object = blogs.find(b => b._id === action.blog._id) || {}

    return [...old, { ...liked, likes: liked.likes + 1 }]
  case 'COMMENT_BLOG':
    old = blogs.filter(b => b._id !== action.data.blog)
    const commented : Object = blogs.find(b => b._id === action.data.blog) || {}

    return [...old, { ...commented, comments: [...commented.comments, action.data] }]
  case 'CREATE_BLOG':
    return [...blogs, action.data ]
  case 'REMOVE_BLOG':
    return blogs.filter(b => b._id !== action.blog.id)
  case 'INIT_BLOGS':
    return action.data
  default:
    return blogs
  }
}


export const likeBlog = (blog : Object) : Function => {
  return async (d : Function) => {
    await blogService.update(blog._id, { ...blog, likes: blog.likes + 1 })
    d({
      type: 'LIKE_BLOG',
      blog
    })
  }
}

export const removeBlog = (blog : Object) : Function => {
  return async (d : Function) => {
    await blogService.remove(blog._id)
    d({
      type: 'REMOVE_BLOG',
      blog
    })
  }
}

export const commentBlog = (comment : Object) : Function => {
  return async (d : Function) : Promise<Object> => {
    const data : Object = await blogService.comment(comment.blog, comment)
    d({
      type: 'COMMENT_BLOG',
      data
    })
    return data
  }
}

export const createBlog = (blog : Object) : Function => {
  return async (d : Function) : Promise<Object> => {
    const data : Object = await blogService.create(blog)
    d({
      type: 'CREATE_BLOG',
      data
    })
    return data
  }
}

export const initBlogs = () : Function => {
  return async (d : Function) : Promise<Array<Object>> => {
    const data : Array<Object> = await blogService.getAll()
    d({
      type: 'INIT_BLOGS',
      data
    })
    return data
  }
}

export default reducer



